import { eachSeries, setImmediate } from 'async'

/**
 * Clase Antuan
 *
 * Fachada entre Google API y la aplicacin
 */
export default class Antuan {
  /**
   * Constructor de la clase
   */
  constructor() {
    // console.log('version: 96')
    this.sheetData = {
      sheetColumns: [{
        label: 'Column 1',
        index: '1',
      }, {
        label: 'Column 2',
        index: '2',
      }, {
        label: 'Column 3',
        index: '3',
      }],
    }
  }


  /**
   * Muestra una alert generado en el servidor
   * @param {Object} title ttulo de la alerta
   * @param {Object} content Contenido del mensaje de alerta
   * @return {Promise} Devuelve un Promise
   */
  static showGSAlert(title, content) {
    return new Promise((resolve, reject) => {
      google.script.run
      .withSuccessHandler((data) => {
        resolve(data)
      })
      .withFailureHandler((e) => {
        reject(e)
      })
      .showAlert(title, content)
    })
  }

  /**
   * Pide al servidor las etiquetas contenidas en la primera
   * fila de la hoja activa en el spreadsheet
   * @return {Promise} Devuelve un Promise
   */
  getGsSheetData() {
    return new Promise((resolve, reject) => {
      google.script.run
      .withSuccessHandler((data) => {
        this.sheetData.sheetColumns = data
        resolve(this.sheetData.sheetColumns)
      })
      .withFailureHandler((e) => {
        reject(e)
      })
      .sheetColumns()
    })
  }

  /**
   * Obtiene los datos desde la hoja activa en el spreadsheet
   * @return {Promise} Devuelve un Promise
   */
  static getDataFromSpreadsheet() {
    return new Promise((resolve, reject) => {
      google.script.run
      .withSuccessHandler((data) => {
        resolve(data)
      })
      .withFailureHandler((e) => {
        reject(e)
      }).getDataFromActiveSheet()
    })
  }

  /**
   * Copia las hojas del template definido  en copyTemplateSheets
   * @param {boolean} notContainsSet Identifica si contiene sets
   * @return {Promise} Devuelve un Promise
   */
  static copyGSTemplates(notContainsSet) {
    return new Promise((resolve, reject) => {
      google.script.run
      .withSuccessHandler(() => {
        resolve()
      })
      .withFailureHandler((e) => {
        reject(e)
      })
      .copyTemplateSheets(notContainsSet)
    })
  }

  /**
   * Encapsula en una promesa la llamada al servidor para crear la cantidad
   * filas requeridas en las hojas
   * @param {Object[]} signInfo Objeto con datos de los carteles
   * @return {Promise} Devuelve un Promise cuando se hayan creado las filas
   * requeridas para cada hoja
   */
  static createGSSafeEmptyRows(signInfo, notContainsSet) {
    return new Promise((resolve, reject) => {
      google.script.run
      .withSuccessHandler((reqSizes) => {
        resolve(reqSizes)
      })
      .withFailureHandler((e) => {
        reject(e)
      })
      .createSafeEmptyRows(signInfo, notContainsSet)
    })
  }

  /**
   * Elimina las hojas de carteles en caso de que existan
   * @return {Promise} Devuelve un Promise
   */
  static deleteGSSignSheets() {
    return new Promise((resolve, reject) => {
      google.script.run
      .withSuccessHandler(() => {
        resolve()
      })
      .withFailureHandler((e) => {
        reject(e)
      })
      .deleteSignSheets()
    })
  }


  /**
   * Ejecuta las operaciones para la generacin de crteles
   * @param {Object} info Informacin para generar los carteles
   * @return {Promise} Devuelve un Promise
   */
  static async generatePosters(info, callback) {
    // return new Promise((resolve /* reject*/) => {

    // Elimina las hojas en caso de que existan
    await Antuan.deleteGSSignSheets()

    callback('Copiando formatos predefinidos...')
    // Copia hojas desde un template
    try {
      await Antuan.copyGSTemplates(info.set.notContainsSet)
    } catch (e) {
      // alert(`No se pudo copiar la plantilla. Verifique que tiene acceso a la plantilla.\n ${e}`)
      // Antuan.showGSAlert('Copia Plantilla', textAlert)
      throw e
    }

    // Obtiene los datos desde la hoja activa
    callback('Obteniendo datos normalizados...')
    let data
    try {
      data = await Antuan.getDataFromSpreadsheet()
    } catch (e) {
      // alert(`No se pudo copiar la plantilla. Verifique que tiene acceso a la plantilla.\n ${e}`)
      throw e
    }

    // Procesar la informacin
    callback('Procesando Informacin...')
    const signsInfo = Antuan.processInfo(data, info)

    // crea filas necesarias para la insercin de carteles.
    // Nota: Se utiliza para evitar exceder el mximo de filas
    callback('Aadiendo filas a hojas de cartel...')
    try {
      await Antuan.createGSSafeEmptyRows(signsInfo, info.set.notContainsSet)
    } catch (e) {
      // alert(`No se pudo copiar la plantilla. Verifique que tiene acceso a la plantilla.\n ${e}`)
      throw e
    }

    // NOTA: Se divide los set en arreglo de sets. Esto tiene por
    // objetivo que no se excedan los lmites por uso de funciones
    // en el Servidor de Google.

    // subdivide los sets y boxes en sub grupos para
    // no exceder el tiempo de ejecucin
    let groupSize = 100

    // Calcula el tamao del grupo de calculo de sets y divide
    if (signsInfo.sets.length > 200) {
      groupSize = 100
    } else if (signsInfo.sets.length < 10) {
      groupSize = 5
    } else {
      groupSize = signsInfo.sets.length / 3
    }
    const setsArray = []
    const boxesArray = []
    for (let i = 0; i < signsInfo.sets.length; i += groupSize) {
      setsArray.push(signsInfo.sets.slice(i, i + groupSize))
    }

    // Calcula el tamao del grupo de calculo de boxes y divide
    if (signsInfo.boxes.length > 200) {
      groupSize = 100
    } else if (signsInfo.boxes.length < 10) {
      groupSize = 5
    } else {
      groupSize = signsInfo.boxes.length / 2
    }
    for (let i = 0; i < signsInfo.boxes.length; i += groupSize) {
      boxesArray.push(signsInfo.boxes.slice(i, i + groupSize))
    }

    try {
      callback('Generando etiquetas...')
      if (!info.set.notContainsSet) {
        const p1 = Antuan.createSetsPromise(setsArray, signsInfo.sets.length, callback)
        const p2 = Antuan.createBoxesPromise(boxesArray, signsInfo.boxes.length, callback)
        const p3 = Antuan.createGSResumeSign(signsInfo.resume)
        await Promise.all([p1, p2, p3])
      } else {
        await Antuan.createBoxesPromise(boxesArray, signsInfo.boxes.length, callback)
      }
    } catch (e) {
      // alert(`No se pudo copiar la plantilla. Verifique que tiene acceso a la plantilla.\n ${e}`)
      throw e
    }

    try {
      await Antuan.deleteGSFormatRows()
    } catch (e) {
      // alert(`No se pudo copiar la plantilla. Verifique que tiene acceso a la plantilla.\n ${e}`)
      throw e
    }
    callback('Generacin de etiquetas finalizada')
  }

  /**
   * Encapsula en una promesa la ejecucin la creacin de crteles de Sets
   * @param {Object[]} setsArray arreglo de grupos de set
   * @param {Function} textViewUpdateClb callback para cambiar el texto
   *                   del estado de avance
   * @return {Promise} Devuelve un Promise
   */
  static createSetsPromise(setsArray, totalSets, textViewUpdateClb) {
    let signCounter = 1
    return new Promise((resolve, reject) => {
      eachSeries(setsArray, (sets, callback) => {
        // Mensaje a usuario
        textViewUpdateClb(`Generando cartel de set
        ${signCounter} al ${(signCounter + (sets.length - 1))}
        de  ${totalSets}.\nEsta operacin puede tardar un momento...`)
        // Generando el subset
        Antuan.createGSSetSigns(sets, signCounter)
        .then(() => {
          setImmediate(() => {
            signCounter += sets.length
            callback()
          })
        })
        .catch((e) => {
          reject(e)
        })
      },
      () => {
        resolve()
      })
    })
  }

  /**
   * Encapsula en una promesa la ejecucin la creacin de crteles de Cajas
   * @param {Object[]} setsArray arreglo de grupos de set
   * @param {Function} textViewUpdateClb callback para cambiar el texto
   *                   del estado de avance
   * @return {Promise} Devuelve un Promise
   */
  static createBoxesPromise(boxesArray, totalBoxes, textViewUpdateClb) {
    let signCounter = 1
    return new Promise((resolve, reject) => {
      eachSeries(boxesArray, (boxes, callback) => {
        // Mensaje a usuario
        textViewUpdateClb(`Generando cartel de caja
        ${signCounter} al ${(signCounter + (boxes.length - 1))}
        de ${totalBoxes}.\nEsta operacin puede tardar un momento...`)
        // Generando el subset
        Antuan.createGSBoxSigns(boxes, signCounter)
        .then(() => {
          setImmediate(() => {
            signCounter += boxes.length
            callback()
          })
        })
        .catch((e) => {
          reject(e)
        })
      },
      () => {
        resolve()
      })
    })
  }

  /**
   * Elimina las filas con el formato de la etiqueta
   * @return {Promise} Devuelve un Promise
   */
  static deleteGSFormatRows() {
    return new Promise((resolve, reject) => {
      google.script.run
      .withSuccessHandler(() => {
        resolve()
      })
      .withFailureHandler((e) => {
        reject(e)
      })
      .deleteFormatRows()
    })
  }

  /**
   * Crea las etiquetas de sets en la hoja sets
   * @param {Object[]} sets Arreglo de sets
   * @param {Integer} signCounter contador de cartel generado
   * @return {Promise} Devuelve un Promise
   */
  static createGSSetSigns(sets, signCounter) {
    return new Promise((resolve, reject) => {
      google.script.run
      .withSuccessHandler((counter) => {
        resolve(counter)
      })
      .withFailureHandler((e) => {
        reject(e)
      })
      .createSetSigns(sets, signCounter)
    })
  }

  /**
   * Crea las etiquetas de sets en la hoja caja
   * @param {Object[]} boxes Arreglo de boxes
   * @param {Integer} signCounter contador de cartel generado
   * @return {Promise} Devuelve un Promise
   */
  static createGSBoxSigns(boxes, signCounter) {
    return new Promise((resolve, reject) => {
      google.script.run
      .withSuccessHandler((counter) => {
        resolve(counter)
      })
      .withFailureHandler((e) => {
        reject(e)
      })
      .createBoxSigns(boxes, signCounter)
    })
  }

  /**
   * Crea las etiqueta de resumen en la hoja resumen
   * @param {Object}  resume objeto con el resumen
   * @return {Promise} Devuelve un Promise
   */
  static createGSResumeSign(resume) {
    return new Promise((resolve, reject) => {
      google.script.run
      .withSuccessHandler(() => {
        resolve()
      })
      .withFailureHandler((e) => {
        reject(e)
      })
      .createResumeSign(resume)
    })
  }

  /**
   * Crea el arreglo con los objetos que tiene el nombre, talla y
   * cantidad de prendas, agrupando las prendas que contengan el
   * mismo nombre y talla y sumando la cantidad
   * @param {Object[]} rows arreglo con las "filas" de la hoja normalizada
   * @param {Integer} clothingPos Posicin en el arreglo
   * del nombre de la prenda
   * @param {Integer} sizePos Posicin en el arreglo
   * del tamao de la prenda
   * @param {Intger} quantityPos Posicin en el arreglo
   * del quantityPos de la prenda
   * @return {Object[]} Arreglo con los objetos de Prendas
   * {clothing: , size: , quantity: }
   */
  static groupClothes(rows, clothingPos, sizePos, quantityPos) {
    const reducedArray = []
    rows.map(row => ({
      clothing: row[clothingPos],
      size: row[sizePos],
      quantity: parseFloat(row[quantityPos]),
    })).forEach((clothing) => {
      let found = false
      reducedArray.forEach((obj) => {
        if (obj.clothing === clothing.clothing
            && obj.size === clothing.size) {
          // eslint-disable-next-line no-param-reassign
          obj.quantity += clothing.quantity
          // Logger.log('obj.quantity: ' + obj.quantity)
          // Logger.log('clothing.quantity: ' + clothing.quantity)
          found = true
        }
      })
      if (!found) {
        reducedArray.push(clothing)
      }
    })
    return reducedArray
  }

  /**
   * Crea el arreglo con items personalizados
   * @param {Object[]} rows arreglo con las "filas" de la hoja normalizada
   * @param {Integer} customElementsConf objeto con la configuracin
   * definida por el usuario en el wizzard
   * @return {Object[]} Arreglo con los objetos de item personalizados
   * {label: , value: }
   */
  static getCustomItems(rows, customElementsConf) {
    return customElementsConf.map((customElementConf) => {
      // Asigna label
      const customItem = {
        label: customElementConf.label,
      }
      // Asigna value
      if (customElementConf.isRelateByColumn) {
        customItem.value = rows[0][customElementConf.value - 1]
      } else {
        customItem.value = customElementConf.value
      }
      return customItem
    })
  }

  /**
   * Crea el arreglo con elementos requeridos para crear los carteles de caja
   * @param {Object[]} rows arreglo con las "filas" de la hoja normalizada
   * @param {Integer} confData objeto con la configuracin
   * definida por el usuario en el wizzard
   * @return {Object[]} Arreglo con los objetos de set
   */
  static processSets(data, confData) {
    // extrae columna groupBy
    const groupByValuesArray = data.map(row => (
      // -1 debido a que spreadsheet entrega posicin columna y no arreglo
      row[confData.set.groupBy - 1]
    ))

    // extrae label de la columna
    const groupByLabel = groupByValuesArray.shift()

    // Arreglo son valores sin repeticin. Ser utilizado como index
    const groupByUniqueArray = groupByValuesArray.filter((value, index, self) => (
      self.indexOf(value) === index
    ))

    // Crea los set para el listado de elementos nicos
    const sets = groupByUniqueArray.map((groupByValue) => {
      const set = {}
      const setRows = data.filter((row, index, self) => (
        self[index][confData.set.groupBy - 1] === groupByValue
      ))

      // aade los rows del set para que sean utilizados en la generacin de Box
      set.rows = setRows

      // Define los items fijos a partir de la primera fila
      set.groupBy = groupByLabel
      set.groupByValue = setRows[0][confData.set.groupBy - 1]
      set.serialNumber = setRows[0][confData.set.correlativeNumber - 1]

      // Define los customItems a partir de la primera fila
      set.customItems = Antuan.getCustomItems(setRows, confData.set.customItems)

      // Define arreglo de prendas
      set.clothes = Antuan.groupClothes(
        setRows,
        confData.clothingInfo.colClothing - 1,
        confData.clothingInfo.colSize - 1,
        confData.clothingInfo.colAmount - 1 // eslint-disable-line comma-dangle
      )
      return set
    })

    return sets
  }

  /**
   * Crea el arreglo con elementos requeridos para crear los carteles de caja
   * @param {Object[]} rows arreglo con las "filas" de la hoja normalizada
   * @param {Integer} confData objeto con la configuracin
   * definida por el usuario en el wizzard
   * @return {Object[]} Arreglo con los objetos de caja
   */
  static processBoxes(sets, confData) {
    const setGroups = []
    let setGroup = []
    let boxCounter = 0
    let quantitySetPerBox

    // Si no hay sets 'setsAmounts' ser igual a la cantidad de sets
    // y en el servidor se utilizar el template 'caja_no_set'
    if (!confData.set.notContainsSet) {
      quantitySetPerBox = confData.box.setsAmount
    } else {
      quantitySetPerBox = sets.length
    }

    // Divide el arreglo de sets en sub arreglos
    sets.forEach((set) => {
      if (boxCounter % quantitySetPerBox === 0) {
        setGroup = []
        setGroups.push(setGroup)
      }
      setGroup.push(set)
      boxCounter += 1
    })

    // Genera el contenido de Box en funcin de los sets
    const boxes = setGroups.map((setsItems) => {
      const box = {}

      // Define los items fijos
      box.provider = confData.box.provider
      box.to = confData.box.receiver
      box.orderNumber = confData.box.purchaseOrder

      // Define los customItems a partir de la primera fila
      box.customItems = Antuan.getCustomItems(sets[0].rows, confData.box.customItems)

      // arreglo para agrupar prendas
      let rows = []
      // arreglo para contenido del set de los tems.
      const setItems = []
      setsItems.forEach((set) => {
        setItems.push(
          {
            groupByValue: set.groupByValue,
            serialNumber: set.serialNumber // eslint-disable-line comma-dangle
          },
        )
        rows = rows.concat(set.rows)
      })

      // aade arreglo de los sets al objeto boxes
      box.sets = setItems

      // Define arreglo de prendas
      box.clothes = Antuan.groupClothes(
        rows,
        confData.clothingInfo.colClothing - 1,
        confData.clothingInfo.colSize - 1,
        confData.clothingInfo.colAmount - 1 // eslint-disable-line comma-dangle
      )
      return box
    })

    return boxes
  }

  /**
   * Crea objeto con elementos requeridos para crear los carteles de resumen
   * @param {Object[]} rows arreglo con las "filas" de la hoja normalizada
   * @param {Integer} confData objeto con la configuracin
   * definida por el usuario en el wizzard
   * @return {Object} Objetos de resumen
   */
  static processResume(data, confData) {
    const resume = {}
    // extrae la fila de labels
    data.shift()

    // Define los items fijos a partir de la primera fila
    resume.provider = confData.box.provider
    resume.to = confData.box.receiver
    resume.orderNumber = confData.box.purchaseOrder

    // Define arreglo de prendas
    resume.clothes = Antuan.groupClothes(
      data,
      confData.clothingInfo.colClothing - 1,
      confData.clothingInfo.colSize - 1,
      confData.clothingInfo.colAmount - 1 // eslint-disable-line comma-dangle
    )
    return resume
  }

  /**
   * Procesa los datos y el objeo
   * @param {Object} info Informacin para generar los carteles
   * @return {Promise} Devuelve un Promise
   */
  /**
   * Procesa los datos y el objeto de configuracin del usuario para
   * obtener el un objeto con los datos necesarios para la construccin de
   * los carteles.
   * @param {Object[]} rows arreglo con las "filas" de la hoja normalizada
   * @param {Integer} confData objeto con la configuracin
   * definida por el usuario en el wizzard
   * @return {Object} Objeto con los objetos necesarios para la contrucin
   * los carteles
   */
  static processInfo(data, confData) {
    const setItems = Antuan.processSets(data, confData)
    const boxItems = Antuan.processBoxes(setItems, confData)
    const resumeItem = Antuan.processResume(data, confData)

    const signsInfo = {
      sets: setItems,
      boxes: boxItems,
      resume: resumeItem,
    }
    // Logger.log(signInfo)
    return signsInfo
  }

}



